  @import '../../styles/theme.scss';

  .button {
    font-family: $ff-sans-bold;
    color: $c-black;
    background-color: $c-white;
    border: 2px solid $c-black;
    display: inline-block;
    cursor: pointer;
    padding: 0.4em 1em;
    margin: 0 2px;
    outline: none;
  }
  button:focus {
    outline: 0;
  }
  .button:hover {
    color: $c-white;
    background-color: $c-black;
  }
  .button.is-action {
    border: none;
    color: $c-white;
    background-color: $c-blue;
  }
  .button.is-action:hover {
    background-color: $c-black;
    border-color: $c-black;
  }
  .button.is-compact {
    @include fluid-font-size($fs-small);
    padding: 5px 10px;
  }
  a.button {
    text-decoration: none;
  }
