import { readable } from 'svelte/store';
import { csv } from 'd3-fetch';
import { trim, forEach, map } from 'lodash-es';

export const areaData = readable([], set => {
  csv('./data/areas.csv').then(d => {
    set(d);
  });
});

export const impactData = readable([], set => {
  csv('./data/impacts.csv', (d) => {
    return ({...d, name: trim(d.name)})
  }).then(d => {
    set(d);
  });
});

export const issueData = readable([], set => {
  csv('./data/issues.csv').then(d => {
    set(d);
  });
});

export const impactActorData = readable([], set => {
  csv('./data/actors.csv', (d,i) => {    
    const geographyDict = {};
    const geographyArray = map(d.geography.split(','), e => trim(e));
    forEach(geographyArray, e => {
      geographyDict[e] = 1;
    });
    return ({id: i,...d, geographyDict})
  }).then(d => {
    set(d);
  });
});

