<script>
  import List from '../components/ui/List.svelte';
  import ImpactDetails from '../components/ui/ImpactDetails.svelte';

  import {
    navId,
    areaId,
    impactId,
    categoryId,
    geographyId,
  } from '../state/uiState';
  import { activeAreas, activeImpacts, selectedImpact } from '../data/dataApi';

  $: areas = $activeAreas.map(d => ({
    id: d.id,
    name: d.name,
    count: d.count,
    isActive: d.isActive,
    isHighlighted: d.isHighlighted,
  }));

  $: impacts = $activeImpacts.map(d => ({
    id: d.id,
    name: d.name,
    count: d.count,
    isActive: d.isActive,
    isHighlighted: d.isHighlighted,
  }));
</script>

<style lang="scss">
  @import '../styles/theme.scss';
  .app__cols {
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
    flex: 1;
    overflow: auto;
  }
  .app__col {
    display: flex;
    box-sizing: border-box;
    flex: 1;
    flex-shrink: 1;
  }
  .app__big-col {
    display: flex;
    box-sizing: border-box;
    flex: 1;
    @media #{$m-min-lg} {
      flex-direction: row;
      flex: 2;
    }
  }
</style>

<div class="app__cols">
  <div class="app__col">
    <List
      title="{areas.length > 1 ? 'Justice Areas' : 'Justice Area'}"
      items="{areas}"
      sideBorder="{true}"
      sortItems="{[
        { id: 'name', label: 'Name', direction: 'asc' },
        { id: 'count', label: 'Issue Count', direction: 'desc' },
      ]}"
      sortBy="name"
      showFilterIcon
      showDistribution
      on:click="{d => {
        if ($areaId == d.detail) {
          areaId.set('');
        } else {
          areaId.set(d.detail);
        }
      }}" />
  </div>
  <div class="app__col">
    <List
      title="{impacts.length > 1 ? 'Impacts' : 'Impact'}"
      items="{impacts}"
      sideBorder="{$impactId !== ''}"
      sortItems="{[
        { id: 'name', label: 'Name', direction: 'asc' },
        { id: 'count', label: 'Issue count', direction: 'desc' },
      ]}"
      sortBy="name"
      on:click="{d => {
        if ($impactId == d.detail) {
          impactId.set('');
        } else {
          impactId.set(d.detail);
        }
      }}" />
  </div>
  {#if $impactId !== '' && $selectedImpact !== undefined}
    <div class="app__big-col">
      <ImpactDetails
        name="{$selectedImpact.name}"
        issues="{$selectedImpact.issues}"
        actors="{$selectedImpact.actors}"
        on:close="{() => {
          impactId.set('');
        }}"
        on:click-badge="{d => {
          if (d.detail.type === 'category') {
            categoryId.set(d.detail.id);
            geographyId.set('');
          } else if (d.detail.type === 'geography') {
            geographyId.set(d.detail.id);
            categoryId.set('');
          }
          navId.set('actors');
        }}" />
    </div>
  {/if}
</div>
