import { derived } from 'svelte/store';
import { areaData, impactData, issueData, impactActorData } from './dataStore';
import { map,groupBy, orderBy, keyBy, filter, isEmpty, forEach, values } from 'lodash-es';
import { areaId, impactId, geographyId, categoryId } from '../state/uiState';

export const actorsByGeography = derived([impactActorData], ([$impactActorData]) => {
  if($impactActorData.length === 0) {
    return [];
  }
  const items = {}
  forEach($impactActorData, d => {
    forEach(d.geographyDict, (g,k) => {
      if(items[k] === undefined) {
        items[k] = [];  
      }
      items[k].push(d);
    })
  });

  let index = 1;

  return orderBy(map(items, (d,k) => {
    return {'id': index++,'name': k, 'items': d }
  }), 'name');
});

export const geographyById = derived([actorsByGeography], ([$actorsByGeography]) => {
  return keyBy($actorsByGeography,'id');
});

export const geographyByName = derived([actorsByGeography], ([$actorsByGeography]) => {
  return keyBy($actorsByGeography,'name');
});


export const actorsByCategory = derived([impactActorData], ([$impactActorData]) => {
  if($impactActorData.length === 0) {
    return [];
  }
  let index = 1;
  return orderBy(map(groupBy($impactActorData, 'category'), (d,k) => {
    return {'id': index++,'name': k, 'items': d }
  }), 'name');
});

export const categoryById = derived([actorsByCategory], ([$actorsByCategory]) => {
  return keyBy($actorsByCategory,'id');
});

export const categoryByName = derived([actorsByCategory], ([$actorsByCategory]) => {
  return keyBy($actorsByCategory,'name');
});

export const impactActors = derived([impactActorData, categoryByName, geographyByName], ([$impactActorData, $categoryByName, $geographyByName]) => {
  if($impactActorData.length === 0 || isEmpty($categoryByName) || isEmpty($geographyByName) ) {
    return [];
  }

  return map($impactActorData, d => ({
    ...d, 
    categories: [{id: $categoryByName[d.category].id, name: d.category}],
    geographies: map(d.geographyDict, (g,k) => {
      return {id: $geographyByName[k].id, name: k}
    })
  }))
});


export const areas = derived([areaData, impactActors, issueData], ([$areaData, $impactActors, $issueData]) => {
  
  if($areaData.length === 0 || $impactActors.length === 0 || $issueData.length === 0) {
    return [];
  }

  const output = keyBy($areaData, 'id');
  const actors = groupBy($impactActors, 'area_id');
  const issues = groupBy($issueData, 'area_id');

  forEach(actors, (items,groupId) => {
    if (output[groupId]['items'] === undefined) {
      output[groupId]['items'] = {}
    }
    forEach(groupBy(items, 'impact_id'), (d,k) => {
      if(output[groupId].items[k] == undefined) {
        output[groupId].items[k] = {actors: 0, issues: 0};  
      }
      output[groupId].items[k].actors = d.length;
    })
  })

  forEach(issues, (items,groupId) => {
    if (output[groupId]['items'] === undefined) {
      output[groupId]['items'] = {}
    }
    forEach(groupBy(items, 'impact_id'), (d,k) => {
      if(output[groupId].items[k] == undefined) {
        output[groupId].items[k] = {actors: 0, issues: 0};  
      }
      output[groupId].items[k].issues = d.length;
    })
  })

  return values(output);
});

export const impacts = derived([impactData, impactActors, issueData], ([$impactData, $impactActors, $issueData]) => {
 
  if($impactData.length === 0 || $impactActors.length === 0 || $issueData.length === 0) {
    return [];
  }

  const output = keyBy($impactData, 'id');
  const actors = groupBy($impactActors, 'impact_id');
  const issues = groupBy($issueData, 'impact_id');


  forEach(actors, (items,groupId) => {
    if (output[groupId]['items'] === undefined) {
      output[groupId]['items'] = {}
    }
    forEach(groupBy(items, 'area_id'), (d,k) => {
      if(output[groupId].items[k] == undefined) {
        output[groupId].items[k] = {actors: [], issues: []};  
      }
      output[groupId].items[k].actors = d;
    })
  })

  forEach(issues, (items,groupId) => {
    if (output[groupId]['items'] === undefined) {
      output[groupId]['items'] = {}
    }
    forEach(groupBy(items, 'area_id'), (d,k) => {
      if(output[groupId].items[k] == undefined) {
        output[groupId].items[k] = {actors: [], issues: []};  
      }
      output[groupId].items[k].issues = d;
    })
  })

  return values(output);
});

export const activeAreas = derived([areas,areaId,impactId], ([$areas,$areaId,$impactId]) => {
  let items = $areas;
  if($impactId !== '') {
    items = filter(items, d => d.items[$impactId] !== undefined);
  }
  
  return map(items, d => {
    let count = 0;
    forEach(d.items, (e,k) => {
      if($impactId !== '' && $impactId === k || $impactId === '') {
        count += e.issues;  
      } 
    })

    return ({
      id: d.id,
      name: d.name, 
      count: count,
      isActive: $areaId === d.id,
    })
  })
});

export const activeImpacts = derived([impacts,areaId,impactId], ([$impacts,$areaId,$impactId]) => {
  let items = $impacts;
  if($areaId !== '') {
    items = filter(items, d => d.items[$areaId] !== undefined);
  }

  return map(items, (d) => {
    const actors = [];
    const issues = [];

    forEach(d.items, (item,k) => {
      if($areaId !== '' && $areaId === k || $areaId === '') {
        forEach(item.actors, (e) => {
          actors.push(e);  
        })
        forEach(item.issues, (e) => {
          issues.push(e);  
        })
      }
    })

    return ({
      id: d.id,
      name: d.name, 
      actors: orderBy(actors,'name'), 
      issues: orderBy(issues,'name'),
      count: issues.length,
      isActive: $impactId === d.id,
    })
  })
});


export const impactByIds = derived([activeImpacts], ([$activeImpacts]) => {
  return keyBy($activeImpacts,'id');
});

export const selectedImpact = derived([impactByIds,impactId], ([$impactByIds,$impactId]) => {
  if (isEmpty($impactByIds) || $impactId === ''){
    return undefined;
  }
  return $impactByIds[$impactId];
});






const filterItems = (items, filterId, dict, key) => {
  return filter(map(items, d => {
    return {...d, items: filter(d.items, e => { 
      return e[key] === dict[+filterId].name
    })}
  }), d => d.items.length > 0 )
} 

const filterItemsByDict = (items, filterId, dict, key) => {
  return filter(map(items, d => {
    return {...d, items: filter(d.items, e => { 
      return e[key][dict[+filterId].name] !== undefined
    })}
  }), d => d.items.length > 0 )
} 

export const activeGeographies = derived([actorsByGeography, geographyId, categoryId, categoryById], ([$actorsByGeography, $geographyId, $categoryId, $categoryById]) => {
  if (isEmpty($categoryById ) || $actorsByGeography.length === 0){
    return [];
  }

  let items = $actorsByGeography;
  if($categoryId !== '') {
    items = filterItems(items,+$categoryId,$categoryById,'category');
  }

  return map(items, d => ({
    ...d, 
    count: d.items.length,
    isActive: $geographyId !== '' && +$geographyId === d.id,
  }))
});

export const activeCategories = derived([actorsByCategory, geographyId,geographyById, categoryId], ([$actorsByCategory, $geographyId, $geographyById, $categoryId]) => {
  
  if (isEmpty($geographyById) || $actorsByCategory.length === 0){
    return [];
  }
 
  let items = $actorsByCategory;
  if($geographyId !== '') {
    items = filterItemsByDict(items,+$geographyId,$geographyById,'geographyDict');
  }

  return map(items, d => ({
    ...d, 
    count: d.items.length,
    isActive: $categoryId !== '' && +$categoryId === d.id,
  }))
});



export const activeActors = derived([impactActors, geographyId,geographyById, categoryId,categoryById], ([$impactActors, $geographyId, $geographyById, $categoryId, $categoryById]) => {
  if ($impactActors.length === 0 || isEmpty($categoryById) || isEmpty($geographyById)){
    return [];
  }

  if($geographyId === '' && $categoryId === '' ) {
    return $impactActors;
  } else {
    return filter($impactActors, d => {
      if($geographyId !== '' && d.geographyDict[$geographyById[$geographyId].name] === undefined ) {
        return false
      }
      if($categoryId !== '' && $categoryById[$categoryId].name !== d.category ) {
        return false
      }
      return true;
    })
  }
});



