<script>
  import List from '../components/ui/List.svelte';

  import { categoryId, geographyId } from '../state/uiState';
  import {
    activeGeographies,
    activeCategories,
    activeActors,
  } from '../data/dataApi';

  $: geographies = $activeGeographies.map(d => ({
    id: d.id,
    name: d.name,
    count: d.count,
    isActive: d.isActive,
    isHighlighted: d.isHighlighted,
  }));

  $: categories = $activeCategories.map(d => ({
    id: d.id,
    name: d.name,
    count: d.count,
    isActive: d.isActive,
    isHighlighted: d.isHighlighted,
  }));

  $: actors = $activeActors.map(d => ({
    id: d.id,
    name: d.name,
    nameHtml: d.name_html,
    categories: d.categories,
    geographies: d.geographies,
  }));
</script>

<style lang="scss">
  @import '../styles/theme.scss';
  .app__cols {
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
    flex: 1;
    overflow: auto;
  }
  .app__col {
    display: flex;
    box-sizing: border-box;
    flex: 1;
    flex-shrink: 1;
  }
  .app__big-col {
    display: flex;
    box-sizing: border-box;
    flex: 1;
    @media #{$m-min-lg} {
      flex-direction: row;
      flex: 2;
    }
  }
</style>

<div class="app__cols">
  <div class="app__col">
    <List
      title="{categories.length > 1 ? 'Categories' : 'Category'}"
      items="{categories}"
      sortItems="{[
        { id: 'name', label: 'Name', direction: 'asc' },
        { id: 'count', label: 'Count', direction: 'desc' },
      ]}"
      sortBy="name"
      showFilterIcon
      showDistribution
      sideBorder="{true}"
      on:click="{d => {
        if ($categoryId == d.detail) {
          categoryId.set('');
        } else {
          categoryId.set(d.detail);
        }
      }}" />
  </div>
  <div class="app__col">
    <List
      title="{geographies.length > 1 ? 'Geographies' : 'Geography'}"
      items="{geographies}"
      sortItems="{[
        { id: 'name', label: 'Name', direction: 'asc' },
        { id: 'count', label: 'Count', direction: 'desc' },
      ]}"
      sortBy="name"
      showFilterIcon
      showDistribution
      sideBorder="{true}"
      on:click="{d => {
        if ($geographyId == d.detail) {
          geographyId.set('');
        } else {
          geographyId.set(d.detail);
        }
      }}" />
  </div>
  <div class="app__big-col">
    <List
      title="{actors.length > 1 ? 'Actors' : 'Actor'}"
      type="actor"
      sortItems="{[{ id: 'name', label: 'Name', direction: 'asc' }]}"
      sortBy="name"
      items="{actors}"
      on:click-badge="{d => {
        if (d.detail.type === 'category') {
          categoryId.set(d.detail.id);
          geographyId.set('');
        } else if (d.detail.type === 'geography') {
          geographyId.set(d.detail.id);
          categoryId.set('');
        }
      }}" />
  </div>
</div>
