<script>
  import SubHeader from './SubHeader.svelte';
  import ModeTabs from './ModeTabs.svelte';
  import IssueItem from './IssueItem.svelte';
  import ActorItem from './ActorItem.svelte';

  export let name = '';
  export let actors = [];
  export let issues = [];

  $: actorsLastIndex = actors.length - 1;
  $: issuesLastIndex = issues.length - 1;

  $: console.log(issues);

  let modeId = 'issues';
  $: modeOptions = [
    {
      id: 'issues',
      label: `Issue${issues.length > 1 ? 's' : ''}`,
      info: issues.length,
    },
    {
      id: 'actors',
      label: `Actor${actors.length > 1 ? 's' : ''}`,
      info: actors.length,
    },
  ];

</script>

<style lang="scss">
  @import '../../styles/theme.scss';
  .impact {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    box-shadow: -3px 0 6px rgba(0, 0, 0, 0.12);
    z-index: 10;
  }
  .impact__nav {
    background-color: $c-gray-05;
  }
  .impact__content {
    // border-top: 1px solid $c-black;
    display: flex;
    flex-direction: column;
    flex: 1;
    overflow: auto;
    width: 100%;
    box-sizing: border-box;
    @include fluid-space(padding, $sp-xsmall);
  }
  .impact__header {
    font-family: $ff-sans-bold;
  }

</style>

<div class="impact">
  <SubHeader isLight showCloseBtn on:close>
    <h2 class="impact__header">{name}</h2>
  </SubHeader>
  <div class="impact__nav">
    <ModeTabs bind:modeId modeOptions="{modeOptions}" isCompact />
  </div>
  <div class="impact__content">
    {#if modeId === 'actors'}
      {#each actors as { name_html, categories, geographies }, index}
        <ActorItem
          text="{name_html}"
          categories="{categories}"
          geographies="{geographies}"
          isLast="{actorsLastIndex == index}"
          on:click-badge />
      {/each}
    {:else if modeId === 'issues'}
      {#each issues as { name_html, info_html, geography }, index}
        <IssueItem
          text="{name_html}"
          info="{info_html}"
          geography="{geography}"
          isLast="{issuesLastIndex == index}" />
      {/each}
    {/if}
  </div>
</div>
