<script>
  import { createEventDispatcher } from 'svelte';
  import { orderBy } from 'lodash-es';
  import { sum } from 'd3-array';
  import { scaleLinear } from 'd3-scale';

  import ListItem from './ListItem.svelte';
  import ActorItem from './ActorItem.svelte';

  import SubHeader from './SubHeader.svelte';
  import Sorting from './Sorting.svelte';
  import Icon from './Icon.svelte';

  export let items = [];
  export let title = '';
  export let sideBorder = false;
  export let sortItems = [];
  export let sortBy = 'name';
  export let type = 'list';
  export let showFilterIcon = false;
  export let showDistribution = true;

  let sortingBy = sortBy;
  let sortingDirection = undefined;

  $: sortedItems =
    sortItems.length && sortingDirection !== null
      ? orderBy(items, sortingBy, sortingDirection)
      : items;
  $: distributionScale = showDistribution
    ? scaleLinear()
        .domain([0, sum(items, d => d.count)])
        .range([0, 100])
    : undefined;

  const dispatch = createEventDispatcher();
</script>

<style lang="scss">
  @import '../../styles/theme.scss';
  .list {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    box-sizing: border-box;

    &.has-sideborder {
      border-right: 1px solid $c-gray-20;
    }
  }
  .list__content {
    display: flex;
    flex-direction: column;
    flex: 1;
    overflow: auto;
    width: 100%;
    box-sizing: border-box;
  }

  .list__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .list__header-title {
    display: flex;
    flex-direction: row;

    h2 {
      display: flex;
      flex-wrap: nowrap;
      align-items: center;
      font-family: $ff-sans-bold;
      @include fluid-space(margin-right, $sp-xxsmall);

      .filter-icon {
        display: flex;
        margin-right: 4px;
        color: $c-black;
      }
    }
    span {
      color: $c-gray-60;
    }
  }
</style>

<div class="list" class:has-sideborder="{sideBorder}">
  <SubHeader>
    <div class="list__header">
      <div class="list__header-title">
        <h2>
          <span class="filter-icon"
            >{#if showFilterIcon}
              <Icon name="filter" />
            {/if}</span>
          {title}
        </h2>
        <span>{items.length}</span>
      </div>
      {#if sortItems.length !== 0}
        <Sorting
          items="{sortItems}"
          bind:activeId="{sortingBy}"
          bind:activeDirection="{sortingDirection}" />
      {/if}
    </div>
  </SubHeader>
  <div class="list__content">
    {#each sortedItems as d}
      {#if type === 'list'}
        <ListItem
          name="{d.name}"
          count="{d.count}"
          isActive="{d.isActive}"
          showDistribution="{showDistribution}"
          distributionScale="{distributionScale}"
          on:click="{() => {
            dispatch('click', d.id);
          }}" />
      {:else if type === 'actor'}
        <ActorItem
          text="{d.nameHtml}"
          categories="{d.categories}"
          geographies="{d.geographies}"
          hasPadding
          on:click-badge />
      {/if}
    {/each}
  </div>
</div>
