<script>
  import { createEventDispatcher } from 'svelte';
  import { map } from 'lodash-es';

  import Badge from './Badge.svelte';

  export let text = '';
  export let categories = [];
  export let geographies = [];
  export let isLast = false;
  export let hasPadding = false;

  $: items = [
    ...map(categories, d => ({ ...d, type: 'category', info: 'Category' })),
    ...map(geographies, d => ({ ...d, type: 'geography', info: 'Geography' })),
  ];

  const dispatch = createEventDispatcher();
</script>

<style lang="scss">
  @import '../../styles/theme.scss';
  .actor-info {
    width: 100%;
    @include fluid-space(padding-bottom padding-top, $sp-xsmall);
    border-bottom: 1px solid $c-gray-20;
    box-sizing: border-box;
    line-height: 1.4;

    &.is-last {
      border-bottom: none;
    }
    &.has-padding {
      @include fluid-space(padding-left padding-right, $sp-xsmall);
    }
  }
  :global(.actor-info__content a:first-child) {
    font-family: $ff-sans-bold;
  }

  .actor-info__info {
    @include fluid-space(padding-top, $sp-xxsmall);
  }
</style>

<div
  class="actor-info"
  class:is-last="{isLast}"
  class:has-padding="{hasPadding}">
  <p class="actor-info__content">
    {@html text}
  </p>
  <p class="actor-info__info">
    {#each items as { id, name, type, info }}
      <Badge
        name="{name}"
        info="{info}"
        on:click="{() => {
          dispatch('click-badge', { id, type });
        }}" />
    {/each}
  </p>
</div>
