<script>
  import { onMount, tick } from 'svelte';
  import { get } from 'svelte/store';

  // list of stores to sync with URL state
  export let stores = [];
  export let getFn = [];
  export let setFn = [];

  function isNumber(n) {
    return !isNaN(parseFloat(n)) && !isNaN(n - 0);
  }

  function updateHash(d, i) {
    // simple encoding <store1>/<store2>/â¦
    return tick().then(() => {
      window.location.hash = stores
        .map(d => {
          let v = get(d);
          if (getFn.length && getFn.length >= i && getFn[i] !== undefined) {
            v = getFn[i](v);
          }
          return v;
        })
        .join('/');
    });
  }

  function hashchange() {
    const h = window.location.hash.slice(1).split('/');
    // update stores
    h.forEach((value, i) => {
      if (stores[i] && stores[i] !== value && value !== '' && stores[i].set) {
        let v = value;
        if (setFn.length && setFn.length >= i && setFn[i] !== undefined) {
          v = setFn[i](v);
        }
        stores[i].set(v);
      }
    });
  }

  onMount(() => {
    hashchange();
    // update hash when stores change
    stores.forEach((p, i) => p.subscribe(d => updateHash(d, i)));
  });
</script>

<svelte:window on:hashchange="{hashchange}" />
